import { dispatch, select } from '@wordpress/data';
import { capitalize } from '@plover/utils';

const DEFAULT_STATE = {
  deviceType: 'desktop'
};

const storeOptions = {
  name: 'plover/responsive-device-type',
  options: {
    reducer (state = DEFAULT_STATE, action) {
      switch (action.type) {
        case 'SET_PREVIEW_DEVICE_TYPE':
          return {
            ...state,
            deviceType: action.payload
          };
      }

      return state;
    },
    actions: {
      setPreviewDeviceType: function (deviceType) {
        // post or site editor
        const _ref = 'site-editor' === window.pagenow ? dispatch('core/edit-site') : dispatch('core/edit-post') || false,
          setPreviewDeviceType = _ref.__experimentalSetPreviewDeviceType;

        if (setPreviewDeviceType) {
          setPreviewDeviceType(capitalize(deviceType));
        }

        // customize preview
        if ('customize' === (window ? window.pagenow : undefined)) {
          if (wp.customize) {
            wp.customize.previewedDevice(deviceType);
          }
        }

        return {
          type: 'SET_PREVIEW_DEVICE_TYPE',
          payload: deviceType
        };
      }
    },
    selectors: {
      getPreviewDeviceType: function (state) {

        const core = ('site-editor' === window.pagenow)
          ? (select('core/editor') ?? select('core/edit-site'))
          : select('core/edit-post');

        if (core && core.getDeviceType) {
          return core.getDeviceType().toLowerCase();
        }

        if (core && core.__experimentalGetPreviewDeviceType) {
          return core.__experimentalGetPreviewDeviceType().toLowerCase();
        }

        return state.deviceType;
      }
    }
  }
};

export default storeOptions;
