import { register, createReduxStore, registerStore } from '@wordpress/data';

// register device store
import deviceStore from './device-type/store';

export * from './constants';

if (register) {
  register(createReduxStore(deviceStore.name, deviceStore.options));
} else {
  // WP 5.4 - WP5.6
  registerStore(deviceStore.name, deviceStore.options);
}

export * from './device-type/hooks';
